body {
  --main-bg-color: white;
  --main-color: black;
  --light-color: white;
  --main-button-color: #7a68d2;
  --main-grey-color: #68676c;
  --main-red-color: red;
  background-color: var(--main-bg-color);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a.plainLink {
  text-decoration: none;
  color: inherit;
}

button {
  background-color: var(--main-button-color);
  border-width: 1px;
  border-color: var(--main-button-color);
  color: var(--light-color);
  padding: 10px;
  margin: 2px;
}

button:hover {
  background-color: var(--main-grey-color);
}

button.muteButton {
  position: fixed;
  right: 120px;
  width: 80px;
}

button.startOverButton {
  position: fixed;
  right: 20px;
}

.map {
  position: fixed;
  left: 20px;
  white-space: pre;
}

p.mapBox {
  font-family: Courier, Monaco, monospace;
}

pre.gameOver {
  color: var(--main-red-color);
  position: relative;
  top: 50%;
  text-align: center;
  font-family: monospace;
  font-size : 17px;
  font-weight: bold;
  -webkit-animation-name: example; /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
  animation-name: example;
  animation-duration: 3s;
}
pre.theEnd{
  text-align: center;
  color: var(--main-button-color);
}

@-webkit-keyframes example {
  0%   {font-size : 13px; font-weight: bold;}
  /*25%  {font-size : 14px; font-weight: bold; position: relative; bottom: 20px}
  50%  {font-size : 15px; font-weight: bold; position: relative; bottom: 40px}*/
  100% {font-size : 17px; font-weight: bold; top: 50%; margin-bottom: 50px;}
}

.gameHeader {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  overflow: hidden;
  background-color: var(--main-bg-color);
}

.buttonContainer {
  margin: 5px;
}

.sceneBox {
  text-align: justify;
  color: var(--main-grey-color);
}
.sceneBox:last-of-type{
  color: var(--main-color)
}
.sceneList {
  padding-top: 120px;
  margin-left: 25%;
  margin-right: 25%;
}


.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 800px;
  height: 400px;
  display: block;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  border-radius: 14px;
  max-width: 100%;
  outline: none;
}

img.modal {
  width: auto;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

